export const doQueryData = (model) => {

    let params = {
        token: sessionStorage.getItem("token"),
        userid: sessionStorage.getItem("userId")
    }
    Object.assign(params, model)
    return Api.requestForm2('/countByTime', params)
        .then(res => res);
}
